<template>
  <div id="app">
    <div class="title-bar">
      <h1>Active Wildfires</h1>
      <p>The last 24 hours (on 20/10/2019) of active fire data from NASA's Moderate Resolution Imaging Spectroradiometer (<a href="https://terra.nasa.gov/about/terra-instruments/modis" target="_blank">MODIS</a>) onboard the <a href="https://terra.nasa.gov/" target="_blank">Terra</a> satellite.</p>
    </div>
    <div id="map"></div>
  </div>
</template>

<script>
import mapboxgl from 'mapbox-gl'
import '../node_modules/mapbox-gl/dist/mapbox-gl.css'

export default {
  name: 'app',
  mounted() {
    mapboxgl.accessToken =
      'pk.eyJ1IjoiYW5keXBvdHRzIiwiYSI6ImNrMXlwOGNjMjA0MGszbnA5azc2c2ZvdXQifQ.xBsOyS2SE5Uhh-xmm775cA'

    //init the map
    const map = new mapboxgl.Map({
      container: 'map',
      style: 'mapbox://styles/andypotts/ck1ysaewp1wx51cmrntit259k',
      minzoom: 3,
      center: [-55, -15],
      zoom: 3
    })

    //load fire data
    map.on('load', () => {
      map.addSource('fire-data', {
        type: 'geojson',
        data: './fire-data.geo.json'
      })

      //https://docs.mapbox.com/mapbox-gl-js/example/heatmap-layer/
      map.addLayer({
        id: 'fire-heatmap',
        type: 'heatmap',
        source: 'fire-data',
        maxzoom: 9,
        paint: {
          'heatmap-weight': ['interpolate', ['linear'], ['get', 'mag'], 0, 0, 6, 1],
          'heatmap-intensity': ['interpolate', ['linear'], ['zoom'], 0, 1, 9, 3],
          'heatmap-color': [
            'interpolate',
            ['linear'],
            ['heatmap-density'],
            0,
            'rgba(33,102,172,0)',
            0.2,
            'rgb(103,169,207)',
            0.4,
            'rgb(209,229,240)',
            0.6,
            'rgb(253,219,199)',
            0.8,
            'rgb(239,138,98)',
            1,
            'rgb(178,24,43)'
          ],
          'heatmap-radius': ['interpolate', ['linear'], ['zoom'], 0, 2, 9, 20],
          'heatmap-opacity': ['interpolate', ['linear'], ['zoom'], 7, 1, 9, 0]
        }
      })

      map.addLayer({
        id: 'fire-circles',
        type: 'circle',
        source: 'fire-data',
        paint: {
          'circle-radius': [
            'interpolate',
            ['linear'],
            ['zoom'],
            7,
            ['interpolate', ['linear'], ['get', 'mag'], 1, 1, 6, 4],
            16,
            ['interpolate', ['linear'], ['get', 'mag'], 1, 5, 6, 50]
          ],
          'circle-color': 'rgba(220,35,35,1)',
          'circle-opacity': ['interpolate', ['linear'], ['zoom'], 7, 0, 8, 1]
        }
      })
    })
  }
}
</script>

